<template>
  <div>
    <b-row>
      <div class="title-fs-2 ml-5">{{ $t("SUPPLIERINFORMATION") }}</div>
      <br /><br />
    </b-row>
    <b-form @submit.prevent="submit" class="ml-5">
      <div class="ml-3">
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("SUPPLIERNAME") }}</label>
          </b-col>
          <b-col cols="12" xl="3">
            <b-form-input
              v-model="supplierName"
              maxlength="20"
              class="form-control"
            ></b-form-input>
            <show-errors
              :control="$v.supplierName"
              :field="$t('SUPPLIERNAME')"
            />
          </b-col>
        </b-row>
        <br />
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button variant="outline-primary" class="px-4 required-btnAny">{{
              $t("ANYBUTTON")
            }}</b-button>
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("ADDRESS") }}</label>
          </b-col>
          <b-col cols="12" xl="3">
            <b-form-input
              v-model="supplierAddress"
              class="form-control"
            ></b-form-input>
          </b-col>
        </b-row>
        <br />
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button variant="outline-primary" class="px-4 required-btnAny">{{
              $t("ANYBUTTON")
            }}</b-button>
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("SUPPLIERICONIMG")
            }}</label>
          </b-col>
          <b-col cols="12" xl="3">
            <b-form-input
              v-model="supplierIcon"
              :disabled="true"
              placeholder="xxxxxx.png"
              class="form-control"
            ></b-form-input>
            <p class="text-danger">{{ fileErrorMessage }}</p>
          </b-col>
          <b-col cols="12" xl="4">
            <b-button
              id="fileSelect"
              variant="primary"
              class="file-selection-btn mt-1"
            >
              <label for="avatar">{{ $t("IMGSELUP") }}</label>
            </b-button>
            <input
              id="avatar"
              ref="file"
              @change="fileChange"
              accept=".jpg,.png"
              type="file"
              name="avatar"
              hidden
            />
          </b-col>
        </b-row>
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3"> </b-col>
          <b-col cols="11" xl="3">
            <b-link
              @click="removeSupplierIcon"
              href="javascript:;"
              class="card-link float-left link mt-2"
            >
              <u>{{ $t("SUPPLIERICONIMGDEL") }}</u>
            </b-link>
          </b-col>
        </b-row>
        <br />
        <b-row>
          <b-col cols="12" lg="3" xl="3">
            <b-button
              type="submit"
              variant="primary"
              class="px-4 align-content-center"
              >{{ supplierNo == null ? $t("REGISTER") : $t("UPDATE") }}
              <i v-if="selectLoader" class="fa fa-spinner fa-spin"
            /></b-button>
          </b-col>
        </b-row>
      </div>
    </b-form>
    <hr class="hrType" />
    <b-row>
      <b-col cols="12" xl="3">
        <div class="title-fs-2 ml-5">{{ $t("SUPPLIERPERSON") }}</div>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="border border-dark pl-2">
          <b-form-group>
            <b-form-radio-group
              id="checkbox-group-1"
              v-model="deleteSelectedPersons"
              :options="personsInCharge"
              class="radioControl mb-3 pt-2"
              size="md"
              stacked
            ></b-form-radio-group>
          </b-form-group>
        </div>
        <b-link
          @click="deleteSupplierPersons"
          :disabled="
            supplierNo == null ||
            ((wiz_auth || supplier_auth) &&
              system_auth == false &&
              person_auth == '01')
          "
          class="card-link float-left link mt-2"
        >
          <u>{{ $t("CHECKEDPERSONDELTEXT") }}</u>
        </b-link>
      </b-col>
      <b-col cols="12" xl="4">
        <b-button
          @click="showSupplierPersons"
          :disabled="
            supplierNo == null ||
            ((wiz_auth || supplier_auth) &&
              system_auth == false &&
              person_auth == '01')
          "
          variant="primary"
          class="px-4 mt-1 align-content-center"
          >{{ $t("ADDPERSON") }}</b-button
        >
      </b-col>
    </b-row>

    <b-modal
      id="supplier-persons"
      ref="supplier-persons"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("SELECTCONTACT") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <b-form-checkbox-group
              id="checkbox-group-2"
              v-model="supplierPersonSelect"
              :options="supplierPersons"
              class="radioControl mb-3 pt-2"
              size="md"
              stacked
            ></b-form-checkbox-group>
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            @click="getSelectedPersons"
            variant="primary"
            class="float-right"
            >{{ $t("ADD") }}
          </b-button>
        </b-col>
      </b-row>
    </b-modal>

    <hr class="hrType" />
    <b-row>
      <div class="title-fs-2 ml-5">{{ $t("REDUCTIONPROPOSALMENU") }}</div>
      <br /><br />
    </b-row>
    <b-button
      @click="goToSupplierReductionProposalAddEdit"
      :disabled="supplierNo == null"
      variant="primary"
      class="px-4 align-content-center ml-5"
      >{{ $t("ADDREDUCTIONPROPOSALMENU") }}</b-button
    >
    <br /><br />
    <b-row>
      <b-col>
        <div class="border border-dark pl-2 pr-2 ml-5">
          <b-table
            ref="table"
            :fields="fields"
            :items="tableData"
            :busy="isBusy"
            responsive="sm"
            sticky-header
            fixed
          >
            <template #table-colgroup="scope">
              <col
                v-for="field in scope.fields"
                :key="field.key"
                :style="{
                  width: field.key === 'selectRow' ? '30px' : '110px',
                }"
              />
            </template>
            <template v-slot:cell(selectRow)="item">
              <b-form-radio
                :id="'checkbox-' + item.index"
                v-model="item.item.selectRow"
                :name="'table_radio'"
                @change="toggleSelectAll(item)"
              ></b-form-radio>
            </template>
            <template v-slot:table-busy>
              <div class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
              </div>
            </template>
            <template v-slot:cell(expense_item_nm)="item">
              {{ item.value }}
            </template>
            <template v-slot:cell(reduction_proposal_name)="item">
              {{ item.value }}
            </template>
            <template v-slot:cell(effective_date_st)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(effective_date_ed)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(reduction_proposal_cost)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(Item6)="item">
              <b-button
                @click="goToSupplierReductionProposalAddEdit(item)"
                variant="primary"
                class="btn-sm px-1 btn-min-width"
                >{{ $t("EDIT") }}</b-button
              >
            </template>
          </b-table>
        </div>
      </b-col>
    </b-row>
    <b-row class="d-flex align-items-center">
      <b-col cols="11" xl="6">
        <b-link
          @click="deleteReductionProposalRow"
          :disabled="!checkedItems.length > 0"
          href="javascript:;"
          class="card-link float-left link mt-2 ml-5"
        >
          <u>{{ $t("CHECKEDREDUCTIONPROPOSALMENUDELTEXT") }}</u>
        </b-link>
      </b-col>
    </b-row>
    <br /><br />
  </div>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { mapGetters } from "vuex";
import moment from "moment";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "SupplierAddEdit",
  components: { ShowErrors },
  data() {
    return {
      supplierNo: null,
      supplierName: "",
      supplierAddress: "",
      supplierIcon: "",
      supplierPersonSelect: [],
      supplierPersons: [],
      personsInCharge: [],
      deleteSelectedPersons: null,
      tableData: [],
      checkedItems: [],
      isBusy: false,
      selectLoader: false,
      fileErrorMessage: "",
      wiz_auth: Boolean(localStorage.getItem("wiz_auth")),
      system_auth: Boolean(localStorage.getItem("admin_auth")),
      supplier_auth: Boolean(localStorage.getItem("supplier_auth")),
      person_auth: localStorage.getItem("person_auth"),
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      supplier_data: "getSupplierData",
    }),
    fields() {
      return [
        {
          key: "selectRow",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "expense_item_nm",
          label: this.$t("REGISTRATIONFEE"),
          class: "text-left normal-width",
        },
        {
          key: "reduction_proposal_name",
          label: this.$t("REDUCTIONPROPOSALMENUNAME"),
          class: "text-left normal-width",
        },
        {
          key: "effective_date_st",
          label: this.$t("EXPIRATIONDATESTART"),
          class: "text-center normal-width",
        },
        {
          key: "effective_date_ed",
          label: this.$t("EXPIRATIONDATEEND"),
          class: "text-center normal-width",
        },
        {
          key: "reduction_proposal_cost",
          label: this.$t("PROPOSEDAMOUNT"),
          class: "text-center normal-width",
        },
        {
          key: "Item6",
          label: "",
          class: "text-center normal-width",
        },
      ];
    },
  },
  validations: {
    supplierName: { required },
  },
  async mounted() {
    this.supplierNo = this.supplier_data.id;
    if (this.supplierNo != null) {
      await this.getSupplier();
      await this.getSupplierPersons();
      this.getTableData();
    }
    await this.getPersons();
  },
  methods: {
    /**
     * @description function to get the supplier
     * @name getSupplier
     * @param none
     * @returns nothing
     */
    async getSupplier() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMSUPPLIER,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "supplier_no",
              value: this.supplierNo,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data[0];
        this.supplierName = responseData.supplier_name;
        this.supplierAddress = responseData.supplier_address;
        this.supplierIcon =
          responseData.icon_file_name == "null"
            ? ""
            : responseData.icon_file_name;
      }
    },
    /**
     * @description function trigger when the file change in the file input control
     * @name fileChange
     * @param event
     * @returns nothing
     */
    fileChange(event) {
      let fileSize = 0;
      this.fileErrorMessage = "";
      var ext = event.target.files[0].name.split(".").pop();
      fileSize = Math.round(event.target.files[0].size / 1024);
      if (
        ext == "jpg" ||
        ext == "png" ||
        ext == "jpeg" ||
        ext == "JPG" ||
        ext == "PNG" ||
        ext == "JPEG"
      ) {
        if (fileSize > 1024) {
          this.fileErrorMessage = this.$t("MAXICONSIZE");
          this.supplierIcon = "";
          this.$refs.file.value = null;
          return false;
        }
      } else {
        this.supplierIcon = "";
        this.$refs.file.value = null;
        this.fileErrorMessage = this.$t("UPLOADEXCECUTABLEFILES");
        return false;
      }
      this.supplierIcon = event.target.files[0].name;
    },
    /**
     * @description function to delete the supplier icon from the db
     * @name removeSupplierIcon
     * @param none
     * @returns nothing
     */
    async removeSupplierIcon() {
      if (this.supplierNo != null) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.SUPPLIERUPDATESUPPLIER,
          {
            supplier_no: this.supplierNo,
            update_id: this.user_details.client_account_no,
            icon_path: null,
            icon_file_name: null,
          }
        );
        if (data.success) {
          this.supplierIcon = "";
        }
      }
      this.supplierIcon = "";
      this.$refs.file.value = null;
    },
    /**
     * @description function to add or update the supplier
     * @name submit
     * @param none
     * @returns nothing
     */
    async submit() {
      this.$v.supplierName.$touch();
      if (!this.$v.supplierName.$invalid) {
        this.selectLoader = true;
        var formData = new FormData();
        let url = "";
        if (this.supplierNo == null) {
          formData.append("create_id", this.user_details.client_account_no);
          url = endPoint.SUPPLIERCREATESUPPLIER;
        } else {
          formData.append("supplier_no", this.supplierNo);
          formData.append("update_id", this.user_details.client_account_no);
          url = endPoint.SUPPLIERUPDATESUPPLIER;
        }

        formData.append("supplier_name", this.supplierName);
        formData.append(
          "supplier_address",
          this.supplierAddress ? this.supplierAddress : ""
        );

        formData.append("del_flg", false);
        if (this.$refs.file.files.length > 0)
          formData.append("attachment", this.$refs.file.files[0]);

        const { data } = await BasicInfoApis.postAPI(url, formData);
        this.selectLoader = false;
        if (data.success) {
          if (this.supplierNo == null) {
            this.supplierNo = data.data.supplier_no;
            this.$store.dispatch("updateSupplierData", {
              id: data.data.supplier_no,
            });
            this.$swal({ type: "success", text: this.$t("SUPPLIERADDED") });
          } else {
            this.$swal({ type: "success", text: this.$t("SUPPLIERUPDATED") });
          }
        }
      }
    },
    /**
     * @description function to show the supplier person modal popup
     * @name showSupplierPersons
     * @param none
     * @returns nothing
     */
    showSupplierPersons() {
      this.supplierPersonSelect = [];
      this.$refs["supplier-persons"].show();
    },
    /**
     * @description function to get persons attached to the supplier
     * @name getSupplierPersons
     * @param none
     * @returns nothing
     */
    async getSupplierPersons() {
      this.personsInCharge = [];
      this.supplierPersonSelect = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADWITHJOINMSUPPLIERPERSONINFO,
        {
          limit: 50,
          offset: 0,
          paginate: false,
          fieldlist: [
            "m_supplier_person_info.supplier_person_info_no",
            "m_supplier_person_info.supplier_no",
            "m_supplier_person_info.person_id",
            "m_person.person_name",
          ],
          conditions: [
            {
              key: "m_supplier_person_info.supplier_no",
              value: this.supplierNo,
            },
            {
              key: "m_supplier_person_info.del_flg",
              value: "false",
            },
            {
              key: "m_person.del_flg",
              value: "false",
            },
          ],
          joins: [
            {
              table: "m_person",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_person.person_id",
                  join_operator: "=",
                  join_with: "m_supplier_person_info.person_id",
                },
              ],
            },
          ],
        }
      );

      if (data.success) {
        this.personsInCharge = [];
        let responseData = data.data.data;
        responseData.forEach((item) => {
          this.personsInCharge.push({
            text: item.person_name,
            value: {
              supplier_person_info_no: item.supplier_person_info_no,
              person_id: item.person_id,
            },
          });
        });
      }
    },
    /**
     * @description fucntion to get the list of persons to map to the supplier
     * @name getPersons
     * @param none
     * @returns nothing
     */
    async getPersons() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADALLMPERSON,
        {
          fieldlist: ["person_id", "person_name"],
          conditions: [
            {
              key: "del_flg",
              value: false,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        responseData.forEach((item) => {
          this.supplierPersons.push({
            value: item.person_id,
            text: item.person_name,
          });
        });
      }
    },
    /**
     * @description function to get the selected persons
     * @name getSelectedPersons
     * @param none
     * @returns nothing
     */
    async getSelectedPersons() {
      if (this.supplierPersonSelect.length > 0) {
        let payload = [];
        let personArray = [];
        this.supplierPersonSelect.forEach((item) => {
          let isItemFound = this._.find(
            this.personsInCharge,
            function (person) {
              return person.value.person_id == item;
            }
          );
          if (!isItemFound) {
            personArray.push(item);
          }
        });
        if (personArray.length > 0) {
          personArray.forEach((item) => {
            payload.push({
              supplier_no: this.supplierNo,
              person_id: item,
              del_flg: false,
              create_id: this.user_details.client_account_no,
            });
          });
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERCREATEMSUPPLIERPERSONINFO,
            payload
          );
          if (data.success) {
            this.getSupplierPersons();
          }
        }
      }
      this.$refs["supplier-persons"].hide();
    },
    /**
     * @description function to delete the supplier person which is mapped
     * @name deleteSupplierPersons
     * @param none
     * @returns nothing
     */
    async deleteSupplierPersons() {
      if (this.deleteSelectedPersons != null && this.supplierNo != null) {
        let payload = [];
        payload.push({
          key: "supplier_person_info_no",
          value: [this.deleteSelectedPersons.supplier_person_info_no],
        });

        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERSOFTDELETEMSUPPLIERPERSONINFO,
          {
            conditions: payload,
            update_id: localStorage.getItem("personID"),
          }
        );
        if (data.success) {
          //TODO
          this.getSupplierPersons();
          this.deleteSelectedPersons = null;
        }
      } else {
        this.personsInCharge = this._.filter(this.personsInCharge, (item) => {
          return (
            this.deleteSelectedPersons.supplier_person_info_no !=
            item.value.supplier_person_info_no
          );
        });
        this.deleteSelectedPersons = null;
      }
    },
    /**
     * @description function to read the supplier reduction proposal info based on supplier
     * @name getTableData
     * @param none
     * @returns nothing
     */
    async getTableData() {
      this.tableData = [];
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADWITHJOINMSUPPLIERREDUCTOINPROPOSALINFO,
        {
          limit: 50,
          offset: 0,
          paginate: false,
          fieldlist: [
            "m_supplier_reduction_proposal_info.supplier_reduction_proposal_info_no",
            "m_supplier_reduction_proposal_info.reduction_proposal_name",
            "m_supplier_reduction_proposal_info.effective_date_st",
            "m_supplier_reduction_proposal_info.effective_date_ed",
            "m_supplier_reduction_proposal_info.reduction_proposal_cost",
            "m_expense_item.expense_item_nm",
          ],
          conditions: [
            {
              key: "m_supplier_expense_item_info.supplier_no",
              value: this.supplierNo,
            },
            {
              key: "m_supplier_reduction_proposal_info.del_flg",
              value: false,
            },
          ],
          joins: [
            {
              table: "m_supplier_expense_item_info",
              join_type: "left",
              join_conditions: [
                {
                  join_using:
                    "m_supplier_expense_item_info.supplier_expense_item_info_no",
                  join_operator: "=",
                  join_with:
                    "m_supplier_reduction_proposal_info.supplier_expense_item_info_no",
                },
              ],
            },
            {
              table: "m_expense_item",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_expense_item.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_supplier_expense_item_info.accounts_name_cd",
                },
                {
                  join_using: "m_expense_item.expense_item_cd",
                  join_operator: "=",
                  join_with: "m_supplier_expense_item_info.expense_item_cd",
                },
              ],
            },
          ],
          order_by: [
            {
              key: "m_supplier_reduction_proposal_info.update_dt",
              value: "desc",
            },
          ],
        }
      );
      this.isBusy = false;
      if (data.success) {
        let responseData = data.data.data;
        responseData.forEach((item) => {
          this.tableData.push({
            selectRow: false,
            expense_item_nm: item.expense_item_nm,
            reduction_proposal_name: item.reduction_proposal_name,
            effective_date_st: moment(item.effective_date_st).format(
              "YYYY/MM/DD"
            ),
            effective_date_ed: moment(item.effective_date_ed).format(
              "YYYY/MM/DD"
            ),
            reduction_proposal_cost: new Intl.NumberFormat("jp-JP", {
              style: "decimal",
              currency: "JPY",
            }).format(Number(item.reduction_proposal_cost)),
            supplier_reduction_proposal_info_no:
              item.supplier_reduction_proposal_info_no,
          });
        });
      }

      this.tableData.forEach((element, index) => {
        if (index === this.tableData.length - 1) {
          this.tableData[index].last_element = true;
          return;
        }
      });
    },
    /**
     * @description function to delete the supplier reduction proposal info
     * @name deleteReductionProposalRow
     * @param none
     * @returns nothing
     */
    async deleteReductionProposalRow() {
      let payload = [];
      payload.push({
        key: "supplier_reduction_proposal_info_no",
        value: this._.map(
          this.checkedItems,
          "supplier_reduction_proposal_info_no"
        ),
      });
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERSOFTDELETEMSUPPLIERREDUCTIONPROPOSALINFO,
        {
          conditions: payload,
          update_id: localStorage.getItem("personID"),
        }
      );

      if (data.success) {
        //TODO
        this.getTableData();
      }
    },
    /**
     * @description function to toggle all select checkbox in the table
     * @name toggleSelectAll
     * @param none
     * @returns nothing
     */
    toggleSelectAll(item) {
      item.item.selectRow = !item.item.selectRow;
      this.checkedItems = this.tableData.filter((checkedItem) => {
        if (checkedItem.selectRow == true) {
          return checkedItem;
        }
      });
    },
    /**
     * @description function to navigate to the supplier reduction proposal info add/edit screen
     * @name goToSupplierReductionProposalAddEdit
     * @param none
     * @returns nothing
     */
    goToSupplierReductionProposalAddEdit(item) {
      var url = "/supplier/reduction/proposal/addEdit";
      if (item.item == null) {
        this.$store.dispatch("updateSupplierReductionProposal", {
          id: null,
        });
      } else {
        this.$store.dispatch("updateSupplierReductionProposal", {
          id: item.item.supplier_reduction_proposal_info_no,
        });
      }
      this.$router.push(url);
    },
  },
};
</script>

